#contact {
  display: flex;
  justify-content: center;
  align-items: center;
  .contact-container {
    background: #2d2d2d;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    max-width: 600px;
    width: 88%;
    text-align: center;
    h2 {
      margin-bottom: 20px;
      color: white;
    }
    form {
      display: flex;
      flex-direction: column;
      gap: 15px;
      .form-group {
        display: flex;
        flex-direction: column;
        text-align: left;
        label {
          margin-bottom: 5px;
          color: white;
        }
        input, textarea {
          padding: 10px;
          border: 1px solid #ccc;
          border-radius: 5px;
          font-size: 1rem;
        }
        textarea {
          resize: vertical;
          min-height: 150px;
        }
      }
      button {
        padding: 10px;
        border: none;
        border-radius: 5px;
        background: linear-gradient(135deg, #f44369, #3e3b92);
        color: #fff;
        font-size: 1rem;
        cursor: pointer;
        transition: background-color 0.3s ease, transform 0.3s ease;
        &:hover {
          transform: scale(1.03);
        }
      }
    }
  }
  .status-message {
    color: white;
  }
}
@media (max-width: 470px) { 
  #contact{
    .contact-container {
  width: 70%;
    }
  }
}